.cart-container {
  padding: 12px;
  top: 100%;
  right: 50%;
  position: absolute;
  z-index:1000;
  width: 260px;
  height: 190px;
  border-radius: 7px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.cart-container h4 {
  margin: 0;
}

.product-container {
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.product-container p:first-child {
  margin: 0;
  font-size: 15px;
  font-weight: 400;
  color: hsl(219, 9%, 45%);
  color: var(--Dark-grayish-blue);
}

.product-container p:nth-child(2) {
  margin: 0;
  color: hsl(219, 9%, 45%);
  color: var(--Dark-grayish-blue);
}

.product-container strong {
  color: black;
}

.prices-cart strong {
  padding-left: 13px;
}

.cart-txt {
  font-size: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cart-txt-empty {
  margin: auto;
}

.delete-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
}

.checkout-button {
  border: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: hsl(26, 100%, 55%);
  background-color: var(--Orange);
  font-weight: 700;
  font-size: 16px;
  color: white;
  width: 100%;
  height: 50px;
  padding: 6px;
  border-radius: 10px;
}

.checkout-button:hover {
  opacity: 70%;
  cursor: pointer;
}

:root{
    --Orange: hsl(26, 100%, 55%);
    --Pale-orange: hsl(25, 100%, 94%);
    --Very-dark-blue: hsl(220, 13%, 13%);
    --Dark-grayish-blue: hsl(219, 9%, 45%);
    --Grayish-blue: hsl(220, 14%, 75%);
    --Light-grayish-blue: hsl(223, 64%, 98%);
    --White: hsl(0, 0%, 100%);
    --Black: hsl(0, 0%, 0%);
}

@media (max-width: 985px) {
  /* MOBILE */

  .navbar-brand {
    padding: 10px;
    height: 30px;
  }
  
  .nav-link{
    display: none;
  }

  .navbar-brand{
    height:28px;
    padding-left: 4rem;

  }

  .navbar{
    padding: 10px  !important;
  }

  .image-avatar-link img{
    height: 30px !important;

  }
  

}

.navbar {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
    padding: 1rem;
   
}

.nav-container-one{
    display: flex;
    flex-direction: row;
    align-items: center;
    
}

.nav-container-two{
  position: relative;
}

.nav-items{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-left: 5%;
    
}

.nav-link {
  font-family: "Kumbh Sans", sans-serif;
  position: relative;
  margin: 0 1em;
  text-decoration: none;
  font-size: 16px;
  color:hsl(219, 9%, 45%);
  color:var(--Dark-grayish-blue)
}

.nav-link:hover{
    color: hsl(0, 0%, 0%);
    color: var(--Black);
}

.nav-link::after {
  position: absolute;
  top: 3.2rem;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background-color: hsl(26, 100%, 55%);
  background-color: var(--Orange);
  width: 0%;
  content: "";
  height: 4px;
  transition: all 0.5s;
}

.nav-link:hover::after {
  width: 100%;
}

.image-avatar-link{
    border: none;
}

.image-avatar-link img{
  height: 35px;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: 0.1s ease-in-out;
}

.image-avatar-link img:hover {
  cursor: pointer;
  border: 2px solid hsl(26, 100%, 55%);
  border: 2px solid var(--Orange);
  border-radius: 50%;
}

.icon-cart-link {
  background: transparent;
  width:49px; 
  height:50px;
  border: none;
  fill: hsl(219, 9%, 45%);
  fill: var(--Dark-grayish-blue);
  position: relative;
}

.cart-count{
  position: absolute;
  background-color: hsl(26, 100%, 55%);
  background-color: var(--Orange);
  color:white;
  border-radius: 7px;
  width: 20px;
  font-size: 11px;
  font-weight: 700;
  right:5px ;
  top:6px;
  padding: 1px;
}

.icon-cart-link svg{
  width: 45px;
  height: 30px;
}

.icon-cart-link:hover{
    cursor: pointer;
    fill: hsl(0, 0%, 0%) !important;
    fill: var(--Black) !important;
    font-weight: bold;
}

.avatar-cart-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}


/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 22px;
  height: 18px;
  left: 36px;
  top: 23px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: hsl(219, 9%, 45%);
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background:black;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 70px;
  width: 70px;
}

/* Color/shape of close button cross */
.bm-cross {
  background:hsl(219, 9%, 45%) ;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: white;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.menu-item{
  text-decoration: none;
  color: black;
  padding-top: 1.5rem;
  font-weight: bold;
}

button{
  background: transparent;
}
